import logo from './logo.svg';
import './App.css';
import PageWraper from './Pages/pagewraper';
import About from './Components/about/about';
import CardCreator from './Components/cardCreator/carCreator';
import ListView from './Components/listView/listView';
import personService from './Services/person.service';
import {Route, Link, useLocation} from 'react-router-dom'
import { BrowserRouter } from 'react-router-dom';
import { useParams } from 'react-router';
import ShareHanle from './Components/shareHandle';

function App() {
  return (
    <BrowserRouter>
    <div className="App">
      <Route exact path='/:id' component={ShareHanle} />      
      <PageWraper>
        <About></About>
        <CardCreator></CardCreator>
        <ListView size={9} clan={""} /> 
      </PageWraper>
    </div>
    </BrowserRouter>
  );
}

export default App;
