import React, { Component } from 'react';
import { useParams } from 'react-router-dom';
import personService from '../Services/person.service';
import { Helmet } from 'react-helmet';

class ShareHanle extends Component {
    constructor (props) {
        super(props);
        this.state = {
            imageToShow:null,
            lightboxDisplay: false,
        }
    }

    componentDidMount() {
        const param = this.props.match.params;
        console.log(param.id);
        personService.get(param.id)
            .then(response => {
                console.log(response.data);
                this.setState({
                    imageToShow: response.data,
                    lightboxDisplay: true
                })
            })
            .catch(e => {
                console.log(e);
                this.setState({
                    lightboxDisplay: false
                })
            });
    }

    hideLightBox = () => {
        this.setLightBoxDisplay(false);
    }

    setLightBoxDisplay = (flag) => {
        this.setState({lightboxDisplay : flag});
    }

    render () {
        return (
            <div>
                        
                {this.state.lightboxDisplay ? 
                    <>
                    <Helmet>
                        <title>dadadadad</title>
                        <meta property="og:title" content={this.state.imageToShow.name}/>
                        {console.log(this.state.imageToShow)}
                    </Helmet>
                    <div id="lightbox" onClick={this.hideLightBox}>
                        <div id="view" style={{justifyContent: "space-around"}}>
                            <img id="lightbox-img" src={this.state.imageToShow.card}></img>
                        </div>
                    </div>
                    </>
                    :
                    <></>
                }
            </div>
        );
    }
}
export default ShareHanle;